.skills{
display: flex;
flex-wrap: wrap;
flex-direction: row;
width: 100%;
justify-content: center;
}

.SkillsCard{
  background-color: rgba(70, 70, 70, 0.6);
  margin: 2vh;
  padding: 0 15% 0 15%;
}




@media screen and (max-width: 900px) {
.SkillsCard{
  padding: 0;
}

.infoSkills{
margin-top: 25%;
}


}



